import { ActionBar } from "@taroify/commerce"
import { CartOutlined, ChatOutlined, ShopOutlined, Star } from "@taroify/icons"
import { Text } from "@tarojs/components"
import * as React from "react"
import Block from "../../../components/block"
import Page from "../../../components/page"
import "./index.scss"

function BasicActionBar() {
  return (
    <ActionBar>
      <ActionBar.IconButton>
        <ChatOutlined />
        <Text></Text>
      </ActionBar.IconButton>
      <ActionBar.IconButton>
        <CartOutlined />
        <Text></Text>
      </ActionBar.IconButton>
      <ActionBar.IconButton>
        <ShopOutlined />
        <Text></Text>
      </ActionBar.IconButton>
      <ActionBar.Button></ActionBar.Button>
    </ActionBar>
  )
}

function ActionBarWithBadges() {
  return (
    <ActionBar>
      <ActionBar.IconButton badge>
        <ChatOutlined />
        <Text></Text>
      </ActionBar.IconButton>
      <ActionBar.IconButton badge="5">
        <CartOutlined />
        <Text></Text>
      </ActionBar.IconButton>
      <ActionBar.IconButton badge="12">
        <ShopOutlined />
        <Text></Text>
      </ActionBar.IconButton>
      <ActionBar.ButtonGroup>
        <ActionBar.Button color="warning"></ActionBar.Button>
        <ActionBar.Button color="danger"></ActionBar.Button>
      </ActionBar.ButtonGroup>
    </ActionBar>
  )
}

function ActionBarWithCustomIconButton() {
  return (
    <ActionBar>
      <ActionBar.IconButton>
        <ChatOutlined color="#ee0a24" />
        <Text></Text>
      </ActionBar.IconButton>
      <ActionBar.IconButton>
        <CartOutlined />
        <Text></Text>
      </ActionBar.IconButton>
      <ActionBar.IconButton>
        <Star color="#ff5000" />
        <Text></Text>
      </ActionBar.IconButton>
      <ActionBar.ButtonGroup>
        <ActionBar.Button color="danger"></ActionBar.Button>
        <ActionBar.Button color="warning"></ActionBar.Button>
      </ActionBar.ButtonGroup>
    </ActionBar>
  )
}

function ActionBarWithCustomButton() {
  return (
    <ActionBar>
      <ActionBar.IconButton>
        <ChatOutlined />
        <Text></Text>
      </ActionBar.IconButton>
      <ActionBar.IconButton>
        <CartOutlined />
        <Text></Text>
      </ActionBar.IconButton>
      <ActionBar.ButtonGroup>
        <ActionBar.Button style={{ background: "#be99ff" }}></ActionBar.Button>
        <ActionBar.Button style={{ background: "#7232dd" }}></ActionBar.Button>
      </ActionBar.ButtonGroup>
    </ActionBar>
  )
}

export default function ActionBarDemo() {
  return (
    <Page title="ActionBar " className="action-bar-demo">
      <Block title="">
        <BasicActionBar />
      </Block>
      <Block title="">
        <ActionBarWithBadges />
      </Block>
      <Block title="">
        <ActionBarWithCustomIconButton />
      </Block>
      <Block title="">
        <ActionBarWithCustomButton />
      </Block>
    </Page>
  )
}
